import React from 'react';
import { CssBaseline, Button } from '@material-ui/core';
import resumePDF from 'assets/Shubham_Agarwal.pdf';
import { Document, Page } from 'react-pdf/dist/entry.webpack';
import style from "./viewResume.module.scss";
import SectionHeading from '../../common/sectionHeading';
import Navbar from '../../common/navbar';
import Pagination from '@material-ui/lab/Pagination';
import GetAppIcon from '@material-ui/icons/GetApp';

const ViewResume = () => {
  const [numPages, setNumPages] = React.useState(null);
  const [pageNumber, setPageNumber] = React.useState(1);

  const onDocumentLoadSuccess = ({ numPages }) => {
    console.log({ numPages })
    setNumPages(numPages);
  }

  const changePageNo = (e, value) => {
    setPageNumber(value);
  }

  return (
    <>
      <CssBaseline />
      <Navbar showMenuItems={false} showHome={true} />
      <div className="container text-center mt-2">
        <SectionHeading title='Resume' />
        <div className="text-center mt-3 mb-4">
          <a href="/assets/Shubham_Agarwal.pdf" download>
            <Button
              variant="contained"
              color="primary"
              startIcon={<GetAppIcon />}
            >Download

          </Button>
          </a>
          <p className="mt-2"><code>Last Updated: Oct'20</code></p>
        </div>
        <div className={style.resumeBox}>
          <Document
            file={resumePDF}
            onLoadSuccess={onDocumentLoadSuccess}
          >
            <Page pageNumber={pageNumber} />
          </Document>
          <div className={style.pagination}>
            <Pagination color="primary" count={numPages} shape="rounded" onChange={changePageNo} />
          </div>
        </div>
      </div>
    </>
  );
}

export default ViewResume;