import React from 'react';
import LabelHelper from 'helpers/labelHelper';

const Footer = () => {

  const getFooterText = () => {
    return LabelHelper.FOOTER.TEXT.replace('<year>', new Date().getFullYear())
  }

  return (
    <>
      <div >
        <p className="pt-3 pb-2 pl-1 pl-sm-4 m-0 text-white bg-dark" dangerouslySetInnerHTML={{ __html: getFooterText() }} ></p>
      </div>
    </>
  );
}

export default Footer;