import React from 'react';
import SectionHeading from '../../common/sectionHeading';
import LabelHelper from 'helpers/labelHelper';
import avatar from 'assets/images/avatar.jpg';
import { Avatar, makeStyles, Divider } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  large: {
    width: theme.spacing(10),
    height: theme.spacing(10),
  },
}));

const AboutMe = () => {
  const classes = useStyles();

  const addExperience = () => {
    let totalExperience = (new Date() - new Date('10/10/2016')) / (1000 * 60 * 60 * 24 * 365)

    return LabelHelper.ABOUT_US.CONTENT.replace('<exp>', totalExperience.toFixed(2))
  }

  return (
    <>
      <div id='about' className="container mt-2 mb-4">
        <SectionHeading title={LabelHelper.ABOUT_US.HEADING} />
        <div className="d-flex justify-content-center mb-3">
          <Avatar alt="Shubham Agarwal" src={avatar} className={classes.large} />
        </div>
        <div className="row justify-content-center">
          <div className="col-10 col-sm-8">
            <p className="text-justify" dangerouslySetInnerHTML={{ __html: addExperience() }} />
          </div>
        </div>
      </div>
      <div className="sectionDivider">
        <Divider />
      </div>
    </>
  );
}

export default AboutMe;