import React from 'react';
import SectionHeading from '../../common/sectionHeading';
import style from './contact.module.scss'
import LabelHelper from 'helpers/labelHelper';
import { TextField, withStyles, Button, Icon, Tooltip, Input, Snackbar, CircularProgress } from '@material-ui/core';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import InstagramIcon from '@material-ui/icons/Instagram';
import YouTubeIcon from '@material-ui/icons/YouTube';
import GitHubIcon from '@material-ui/icons/GitHub';
import RoomIcon from '@material-ui/icons/Room';
import EmailIcon from '@material-ui/icons/Email';
import PhoneAndroidIcon from '@material-ui/icons/PhoneAndroid';
import MuiAlert from '@material-ui/lab/Alert';
import axios from 'axios';

function Alert1(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const Contact = () => {
  const [formData, setFormData] = React.useState({
    name: "", email: "", message: ""
  })
  const [formErrors, setFormErrors] = React.useState({
    name: false, email: false, message: false
  })
  const [snackbar, setSnackbar] = React.useState({ status: false, value: 'success', message: '', progress: <Icon>send</Icon> })

  const getFormValue = ({ target }) => {
    setFormErrors({ name: false, email: false, message: false })
    setFormData({ ...formData, [target.name]: target.value });
  }

  const sendEmail = () => {
    setSnackbar({ ...snackbar, progress: <CircularProgress style={{ height: '20px', width: '20px' }} color="inherit" />, disabled: true })

    if (!formData.name || !formData.email || !formData.message) {
      setFormErrors({
        ...formData, name: !formData.name ? true : false,
        email: !formData.email ? true : false, message: !formData.message ? true : false
      })
      setSnackbar({ ...snackbar, progress: <Icon>send</Icon>, disabled: false })

      return false
    }

    const emailData = {
      toEmail: process.env.REACT_APP_TO_EMAIL,
      fromEmail: process.env.REACT_APP_FROM_EMAIL,
      name: formData.name,
      email: formData.email,
      message: formData.message
    }

    axios.post(process.env.REACT_APP_BACKEND_PATH + 'email/github-pages', emailData)
      .then(response => {
        if (response && response.data && response.data.message) {
          setSnackbar({ status: true, value: 'success', message: 'Email sent successfully', progress: <Icon>send</Icon>, disabled: false })
        }
        else {
          throw new Error()
        }
      })
      .catch(error => {
        console.log(error);
        setSnackbar({ status: true, value: 'error', message: 'Something went wrong.', progress: <Icon>send</Icon>, disabled: false })
      })
  }

  const closeSnackBar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setSnackbar({ value: '', message: '', status: false })
  }

  return (
    <>
      <div id='contact' className={`${style.contactSection} pt-2 pb-4`}>
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-6">
              <SectionHeading marginBottom='3' title={LabelHelper.CONTACT.HEADING} />
              <div className={`${style.socialButtons}`}>
                <ul className={`${style.upperRow} list-group list-group justify-content-center pb-3 pb-sm-0`}>
                  <li>
                    <a className="p-3 d-block" target="_blank" href="https://goo.gl/maps/8GS4Q53JC1xdge49A" rel="noopener noreferrer">
                      <RoomIcon color="primary" fontSize="large" />
                      <span className="pl-2 ">Noida, UP India</span>
                    </a>
                  </li>
                  <li>
                    <a className="p-3 d-block" href="mailto:shubhamagarwal1694@gmail.com" rel="noopener noreferrer">
                      <EmailIcon color="primary" fontSize="large" />
                      <Tooltip title="Send email" placement="bottom" aria-label="add">
                        <span className="pl-2 ">shubhamagarwal1694@gmail.com</span>
                      </Tooltip>
                    </a>
                  </li>
                  <li>
                    <a className="p-3 d-block" href="tel:+919198568090" rel="noopener noreferrer">
                      <PhoneAndroidIcon color="primary" fontSize="large" />
                      <Tooltip title="Call" placement="bottom" aria-label="add">
                        <span className="pl-2 ">+91-9198568090</span>
                      </Tooltip>
                    </a>
                  </li>
                </ul>

                <ul className={`${style.urls} list-group list-group-horizontal justify-content-center pb-3 pb-sm-0`}>
                  <li className="p-3">
                    <a href="https://github.com/shubhamagarwal16" target="_blank" className={style.github} rel="noopener noreferrer">
                      <Tooltip title="Visit github" placement="bottom" aria-label="add">
                        <GitHubIcon color="primary" fontSize="large" />
                      </Tooltip>
                    </a>
                  </li>
                  <li className="p-3">
                    <a href="https://www.linkedin.com/in/shubhamagarwal16" target="_blank" className={style.linkedin} rel="noopener noreferrer">
                      <Tooltip title="Visit linkedIn" placement="bottom" aria-label="add">
                        <LinkedInIcon color="primary" fontSize="large" />
                      </Tooltip>
                    </a>
                  </li>
                  <li className="p-3">
                    <a href="https://www.instagram.com/_shubham_agarwal/" target="_blank" className={style.instagram} rel="noopener noreferrer">
                      <Tooltip title="Visit instagram" placement="bottom" aria-label="add">
                        <InstagramIcon color="primary" fontSize="large" />
                      </Tooltip>
                    </a>
                  </li>
                  {/* <li className="p-3">
                    <a href="https://www.youtube.com/channel/UCpKxP-l2TmG0khYNpjn0o1Q" target="_blank" className={style.youtube} rel="noopener noreferrer">
                      <Tooltip title="Visit youtube" placement="bottom" aria-label="add">
                        <YouTubeIcon color="primary" fontSize="large" />
                      </Tooltip>
                    </a>
                  </li> */}
                </ul>
              </div>
            </div>
            <div className={`col-12 col-sm-6 mt-3 text-white ${style.emailWindow}`}>
              <div className={`${style.emailSection} p-4`}>
                <h5 className='text-white' >Ping me -</h5>
                <form noValidate autoComplete="off">
                  <TextField value={formData.name} onChange={getFormValue} name="name" fullWidth margin="dense" placeholder="Name" label="Full Name" color="primary" className={style.inputField} error={formErrors.name} helperText={formErrors.name ? "This field is required" : ""} />

                  <TextField value={formData.email} onChange={getFormValue} name="email" fullWidth margin="dense" placeholder="Email" label="Email Address" color="primary" className={style.inputField} error={formErrors.email} helperText={formErrors.email ? "This field is required" : ""} />

                  <TextField value={formData.message} onChange={getFormValue} name="message" fullWidth margin="dense" placeholder="Message" className={`${style.inputField} mt-4`} multiline rows={4} variant="outlined" color="primary" error={formErrors.message} helperText={formErrors.message ? "This field is required" : ""} />

                  <Button disabled={snackbar.disabled} onClick={sendEmail} variant="contained" color="primary"
                    className='mt-3' endIcon={snackbar.progress} > Send </Button>
                </form>
              </div>
            </div>
            <Snackbar open={snackbar.status} autoHideDuration={3000} onClose={closeSnackBar}>
              <Alert1 onClose={closeSnackBar} severity={snackbar.value}>{snackbar.message}</Alert1>
            </Snackbar>
          </div>
        </div>
      </div>
    </>
  );
}

export default Contact;