import React from 'react';
import SectionHeading from '../../common/sectionHeading';
import { Divider, Paper, LinearProgress, Box, Typography, withStyles } from '@material-ui/core';
import style from './skills.module.scss';
import LabelHelper from 'helpers/labelHelper';

let skillsOffset = 0
const Skills = () => {
  const [skills, setProgress] = React.useState(LabelHelper.SKILLS.SKILL_SET)

  React.useEffect(() => {
    skillsOffset = document.getElementById("skills").offsetTop * .8;
    document.addEventListener('scroll', () => progressBarFilling(skillsOffset))
  }, [])

  React.useEffect(() => {
    progressBarFilling(skillsOffset)
  }, [skills])

  const progressBarFilling = (skillsOffset) => {
    if (window.pageYOffset > skillsOffset) {
      let isChanged = false
      let newProgress = skills.map(item => {
        if (item.initialProgress < item.value) {
          isChanged = true;
          item.initialProgress += 5;
        }
        return item;
      })

      if (isChanged) {
        setTimeout(() => {
          setProgress(newProgress)
        }, 0)
      }
      else {
        document.removeEventListener('scroll', progressBarFilling, true)
      }
    }
  }

  const BorderLinearProgress = withStyles({
    root: {
      height: 30,
      borderRadius: 1,

      '& .colorPrimary': {
        backgroundColor: '#aaa'
      }
    }
  })(LinearProgress);

  const LinearProgressWithLabel = (props) => {
    return (
      <Box display="flex" alignItems="center">
        <Box width="100%" mr={1}>
          <b className={`pl-2 ${style.barTitle}`} >{props.title}</b>
          <BorderLinearProgress variant="determinate" {...props} />
        </Box>
        <Box minWidth={35}>
          <Typography variant="body2" color="textSecondary">{`${Math.round(
            props.value,
          )}%`}
          </Typography>
        </Box>
      </Box>
    );
  }

  return (
    <>
      <div id='skills' className={`${style.skillSection} pt-2 pb-4`}>
        <div className="container">
          <SectionHeading marginBottom='3' title={LabelHelper.SKILLS.HEADING} />
          <div>
            <Paper elevation={3} className={`${style.skillContainer} p-3`} >
              <div className="row">
                {
                  skills.map((item, index) => (
                    <div key={index} className="col-12 col-sm-6 mt-3">
                      <LinearProgressWithLabel className={style['customBgColor' + (index + 1)]} title={item.title} value={item.initialProgress} />
                    </div>
                  ))
                }
              </div>
            </Paper>
          </div>
        </div>
      </div>
      <div className="sectionDivider">
        <Divider />
      </div>
    </>
  );
}

export default Skills;