import React from 'react';
import style from './timeline.module.scss';
import LabelHelper from 'helpers/labelHelper';

const Timeline = () => {
  return (
    <>
      <div className={style.timeline}>
        {
          LabelHelper.TIMELINE.map((item, index) => (
            <div key={index} className={`${style.containerTimeline} ${index % 2 === 0 ? style.timelineLeft : style.timelineRight}`}>
              <div className={style.content}>
                <h3 className="text-primary" >{item.YEAR}</h3>
                <p>{item.TEXT}</p>
              </div>
            </div>

          ))
        }
      </div>
    </>
  );
}

export default Timeline;