import React from 'react';
import clsx from 'clsx';
import logo from 'assets/images/Logo_SA.png'
import style from './navbar.module.scss';
import LabelHelper from 'helpers/labelHelper';
import {
  AppBar, Toolbar, IconButton, Button, makeStyles, ButtonGroup,
  SwipeableDrawer, List, ListItem, ListItemText, Divider, Fab
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import { Link } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';

const useStyles = makeStyles((theme) => ({
  offset: theme.mixins.toolbar,
  root: {
    flexGrow: 1,
  },
  logo: {
    position: "relative",
    paddingRight: "125px"
  },
  logoImg: {
    position: "absolute",
    bottom: "-36px",
    border: "3px solid #eee",
    borderRadius: "100%",
    maxWidth: "100px"
  },
  list: {
    width: 275
  },
  menuButton: {
    // marginRight: theme.spacing(2),
  },
  title: {
    position: "relative",
    top: "18%"
  },
  toTop: {
    position: "fixed",
    bottom: 15,
    right: 20,
    zIndex: 5,
    // transform
  }
}));

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const Navbar = ({ showMenuItems, showHome }) => {
  const classes = useStyles();
  const [openSideBar, setSideBar] = React.useState(false)

  React.useEffect(() => {
    window.addEventListener('scroll', () => {
      if (window.pageYOffset > 45) {
        document.getElementById('logo1').style.maxWidth = '64px';
      }
      else {
        document.getElementById('logo1').style.maxWidth = '100px';
      }
      if (window.pageYOffset > 300) {
        document.getElementById('scrollToTop').classList.remove('navbar_toTop0__1ib_R');//.style.display = 'block';
      }
      else {
        document.getElementById('scrollToTop').classList.add('navbar_toTop0__1ib_R');//.style.display = 'none';
      }
    })
  }, [])

  const toggleDrawer = (open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setSideBar(open);
  };

  const sideBarList = () => (
    <div
      className={clsx(classes.list)}
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        {LabelHelper.NAVBAR.NAV_ITEMS.map((item, index) => (
          <React.Fragment key={index}>
            <ListItemLink href={`#${item.path}`} button>
              <ListItemText primary={item.title} />
            </ListItemLink>
            <Divider />
          </React.Fragment>
        ))}
      </List>
    </div>
  )

  const scrollToSection = (path) => {
    document.querySelector(`#${path}`).scrollIntoView({
      behavior: 'smooth',
      block: "start"
    });
  }

  const scrollToTop = () => {
    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
  }

  return (
    <>
      <AppBar className={style.header} position="sticky">
        <div className="customContainer">
          <Toolbar>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12 col-sm-6">
                  {showMenuItems ? <IconButton onClick={toggleDrawer(true)} edge="start" className={`d-sm-none ${classes.menuButton}`}
                    color="inherit" aria-label="menu">
                    <MenuIcon />
                  </IconButton> : null}

                  <span className={`${classes.logo} ${style.logo}`}>
                    <img id="logo1" alt="LOGO" className={classes.logoImg} src={logo} />
                  </span>

                  <span className={`${classes.title} ${style.title}`}> {LabelHelper.NAVBAR.NAME} </span>
                </div>
                <div className="col-12 col-sm-6 text-center">
                  {showMenuItems ? <ButtonGroup className="d-none d-sm-block" variant="text" color="inherit" aria-label="text primary button group">
                    {
                      LabelHelper.NAVBAR.NAV_ITEMS.map((item, index) => (
                        <Button onClick={() => scrollToSection(item.path)} key={index}>{item.title}</Button>
                      ))
                    }
                  </ButtonGroup> : null}
                  {
                    showHome ?
                      <ButtonGroup className={style.homeButton} variant="text" color="inherit" aria-label="text primary button group">
                        <Button startIcon={<HomeIcon />} > <Link to="/" > Home</Link></Button>
                      </ButtonGroup> : null
                  }
                </div>
              </div>
            </div>
          </Toolbar>
          <SwipeableDrawer
            anchor='left'
            open={openSideBar}
            onClose={toggleDrawer(false)}
            onOpen={toggleDrawer(true)}
          >
            {sideBarList()}
          </SwipeableDrawer>
        </div>
      </AppBar>

      {/* TO TOP */}
      <Fab id="scrollToTop" onClick={scrollToTop} color="primary" className={`${style.toTop0} ${classes.toTop} bounceAnimation back`} aria-label="To Top">
        <ExpandLessIcon />
      </Fab>
    </>
  );
}

Navbar.defaultProps = {
  showMenuItems: true,
  showHome: false
}

export default Navbar;